import { Component } from '@angular/core';
import { IonicPage, NavParams } from 'ionic-angular';
import {News} from "../../providers/news/models/news.model";

/**
 * Generated class for the NewsItemPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-news-item',
  templateUrl: 'news-item.html',
})
export class NewsItemPage {
  post: News;

  constructor(navParams: NavParams) {
    this.post = <News>navParams.get('item');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NewsItemPage');

  }

  public clickedLinkOfWebsite() {
    console.log("The link for this news is: ", this.post.link)
    window.open(this.post.link,'_system', 'location=yes');
  }

}



